import React, { useEffect, useState } from 'react';
import Schedules from './Schedules';
import RoomUtilizationChart from './RoomUtilization';
import { InfoCard } from './InfoCard';

import { 
  fetchTodaysSchedules, 
  fetchTotalClasses, 
  fetchActiveLecturers, 
  fetchTotalRooms, 
  fetchTotalTimetables,
  fetchResourceUtilization
} from "../../utils/Services/dashboardService";
import { 
  Award, 
  Calendar, 
  Users, 
  BookOpen, 
} from 'lucide-react';



const DashboardPage = () => {
  const [dashboardData, setDashboardData] = useState({
    schedules: [],
    totalClasses: 0,
    activeLecturers: 0,
    totalRooms: 0,
    totalTimetables: 0,
    resourceUtilization: []
  });

  useEffect(() => {
    const fetchData = async () => {
      const [
        classes, 
        lecturers, 
        rooms, 
        timetables, 
        schedulesData,
        utilizationData
      ] = await Promise.all([
        fetchTotalClasses(),
        fetchActiveLecturers(),
        fetchTotalRooms(),
        fetchTotalTimetables(),
        fetchTodaysSchedules(),
        fetchResourceUtilization()
      ]);

      setDashboardData({
        schedules: schedulesData,
        totalClasses: classes,
        activeLecturers: lecturers,
        totalRooms: rooms,
        totalTimetables: timetables,
        resourceUtilization: utilizationData
      });
    };

    fetchData();
  }, []);

  return (
    <div className="grid grid-cols-1 gap-6 p-4 lg:grid-cols-4">
      {/* Stat Cards */}
      <div className="col-span-1 lg:col-span-4 grid grid-cols-1 md:grid-cols-4 gap-4">
        <InfoCard 
          title="Total Classes" 
          value={dashboardData.totalClasses} 
          icon={BookOpen}
          color="bg-blue-500"
        />
        <InfoCard 
          title="Active Lecturers" 
          value={dashboardData.activeLecturers} 
          icon={Users}
          color="bg-green-500"
        />
        <InfoCard 
          title="Total Rooms" 
          value={dashboardData.totalRooms} 
          icon={Award}
          color="bg-purple-500"
        />
        <InfoCard 
          title="Timetables" 
          value={dashboardData.totalTimetables} 
          icon={Calendar}
          color="bg-red-500"
        />
      </div>

      {/* Schedule */}
      <div className="col-span-1 lg:col-span-2 space-y-6">
        <Schedules schedules={dashboardData.schedules} />
      </div>

      {/* Resource Utilization */}
      <div className="col-span-1 lg:col-span-2 space-y-6">
        <RoomUtilizationChart data={dashboardData.resourceUtilization} />
      </div>
    </div>
  );
};

export default DashboardPage;