'use client';  
import React, { useState } from 'react';  
import {   
  Calendar, Clock, Users, Home, BookOpen, DoorOpen,   
  Menu, X, School, DoorClosed, ChevronsLeft, ChevronsRight   
} from 'lucide-react';  

// Import all page components  
import DashboardPage from '@/Components/DashboardPage/dashboard';  
import TimetablePage from '@/Components/TimetablePage/timetable';  
import RoomsPage from '@/Components/RoomsPage/rooms';  
import LecturersPage from '@/Components/LecturersPage/lecturers';  
import ClassesPage from '@/Components/ClassesPage/classes';  
import CoursePage from '@/Components/CoursePage/courses';  
import FacultyPage from '@/Components/FacultyPage/faculty';  
import ProgramPage from '@/Components/ProgramPage/program';  
import DepartmentPage from '@/Components/DepartmentPage/departments';  
import TimeslotPage from '@/Components/TimeslotPage/timeslot';  
import ProgramDayspage from '@/Components/ProgramDaysPage/programDays';
import TimetableResourceAnalysis from '@/Components/InsightPage/insight';


const TimetableManagementDashboard = () => {  
  const [activePage, setActivePage] = useState('dashboard');  
  const [menuOpen, setMenuOpen] = useState(false);  
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);  

  const toggleMenu = () => setMenuOpen(!menuOpen);  
  const toggleSidebar = () => setSidebarCollapsed(!sidebarCollapsed);  

  const navigationGroups = [
    {
      title: 'Core Features',
      items: [
        { page: 'dashboard', title: 'Dashboard', icon: Home },
        { page: 'timetable', title: 'Timetable', icon: Calendar },
      ],
    },
    {
      title: 'Management',
      items: [
        { page: 'departments', title: 'Departments', icon: BookOpen },
        { page: 'program', title: 'Programs', icon: DoorOpen },
        { page: 'faculty', title: 'Faculty', icon: School },
        { page: 'courses', title: 'Courses', icon: Clock },
        { page: 'classes', title: 'Classes', icon: Users },
        { page: 'lecturers', title: 'Lecturers', icon: Users },
        { page: 'rooms', title: 'Rooms', icon: DoorClosed },
      ],
    },
    {
      title: 'Configuration',
      items: [
        { page: 'timeslots', title: 'Timeslots', icon: Clock },
        { page: 'programDays', title: 'Working Days', icon: Clock },
      ],
    },
    {
      title: 'Insights',
      items: [
        { page: 'Insights', title: 'Analytics', icon: Clock },
      ],
    },
  ];

  // Page content components  
  const renderPageContent = () => {  
    switch (activePage) {  
      case 'dashboard': return <DashboardPage />;  
      case 'timetable': return <TimetablePage />;  
      case 'faculty': return <FacultyPage />;  
      case 'program': return <ProgramPage />;  
      case 'lecturers': return <LecturersPage />;  
      case 'courses': return <CoursePage />;  
      case 'classes': return <ClassesPage />;  
      case 'rooms': return <RoomsPage />;  
      case 'departments': return <DepartmentPage />;  
      case 'timeslots': return <TimeslotPage/>; 
      case 'programDays': return <ProgramDayspage/>; 
      case 'Insights': return <TimetableResourceAnalysis/>;
     
      default: return <DashboardPage />;  
    }  
  };  

  return (  
    <div className="flex min-h-screen bg-gray-100">  
      {/* Sidebar for Large Screens */}  
      <aside   
        className={`hidden lg:block bg-indigo-800 text-white fixed left-0 top-0 bottom-0 overflow-y-auto transition-all duration-300 ease-in-out z-40 ${  
          sidebarCollapsed ? 'w-16' : 'w-64'  
        }`}  
      >  
        {/* Sidebar Collapse Button */}  
        <button   
          onClick={toggleSidebar}   
          className="absolute top-4 right-4 z-50 text-white hover:bg-indigo-700 rounded-full p-2"  
        >  
          {sidebarCollapsed ? <ChevronsRight className="h-5 w-5" /> : <ChevronsLeft className="h-5 w-5" />}  
        </button>  

        {/* Sidebar Header */}  
        <div className="p-6 text-center text-2xl font-bold border-b border-indigo-700">  
          {!sidebarCollapsed ? (  
            <span>Timetable System</span>  
          ) : (  
            <span className="text-sm">TS</span>  
          )}  
        </div>  

        {/* Navigation */}  
        <nav className="py-4">  
          {navigationGroups.map((group) => (
            <div key={group.title} className="mb-6">
              <h4 className={`px-6 text-sm font-semibold uppercase tracking-wide ${sidebarCollapsed ? 'hidden' : ''}`}>
                {group.title}
              </h4>
              <ul className="space-y-2 mt-2">
                {group.items.map(({ page, title, icon: Icon }) => (
                  <li key={page}>
                    <button
                      onClick={() => setActivePage(page)}
                      className={`w-full px-6 py-3 text-left flex items-center space-x-3 transition-colors ${
                        activePage === page
                          ? 'bg-white text-indigo-800'
                          : 'hover:bg-indigo-700'
                      }`}
                    >
                      <Icon className="h-5 w-5" />
                      {!sidebarCollapsed && <span>{title}</span>}
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          ))}
        </nav>  
      </aside>  

      {/* Mobile Navigation */}  
      <div   
        className={`fixed top-0 left-0 w-64 bg-indigo-800 text-white z-50 transform ${  
          menuOpen ? 'translate-x-0' : '-translate-x-full'  
        } transition-transform duration-300 ease-in-out lg:hidden h-full overflow-y-auto`}  
      >  
        <button onClick={toggleMenu} className="absolute top-4 right-4">  
          <X className="h-6 w-6" />  
        </button>  
        <div className="p-6 text-center text-2xl font-bold border-b border-indigo-700">  
          Timetable System  
        </div>  
        <nav className="py-4">  
          {navigationGroups.map((group) => (
            <div key={group.title} className="mb-6">
              <h4 className="px-6 text-sm font-semibold uppercase tracking-wide">{group.title}</h4>
              <ul className="space-y-2 mt-2">
                {group.items.map(({ page, title, icon: Icon }) => (
                  <li key={page}>
                    <button
                      onClick={() => {
                        setActivePage(page);
                        setMenuOpen(false);
                      }}
                      className={`w-full px-6 py-3 text-left flex items-center space-x-3 transition-colors ${
                        activePage === page
                          ? 'bg-white text-indigo-800'
                          : 'hover:bg-indigo-700'
                      }`}
                    >
                      <Icon className="h-5 w-5" />
                      <span>{title}</span>
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          ))}
        </nav>  
      </div>  

      {/* Top Mobile Header */}  
      <header className="lg:hidden fixed top-0 left-0 w-full bg-blue-900 text-white flex justify-between items-center p-4 z-40">  
        <button onClick={toggleMenu}>  
          {menuOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}  
        </button>  
        <h1 className="text-xl font-bold">Timetable System</h1>  
      </header>  

      {/* Main Content Area */}  
      <main   
        className={`flex-1 transition-all duration-300 ease-in-out lg:ml-64 mt-16 lg:mt-0 p-6 ${  
          sidebarCollapsed ? 'lg:ml-16' : 'lg:ml-64'  
        }`}  
      >  
        {renderPageContent()}  
      </main>  
    </div>  
  );  
};  

export default TimetableManagementDashboard;