import React from 'react';
import { motion } from 'framer-motion';

const Schedules = ({ schedules, animated = false }) => {
  const Wrapper = animated ? motion.div : 'div'; // Use `motion.div` if animations are enabled

  return (
    <div className="col-span-1 sm:col-span-2 lg:col-span-2 bg-white rounded-lg shadow-md p-6">
      <h3 className="text-lg text-green-500 font-semibold mb-4">Today's Schedules</h3>
      <div className="space-y-3 max-h-64 overflow-y-auto">
        {schedules.length > 0 ? (
          schedules.map((schedule, index) => (
            <Wrapper
              key={index}
              className="flex justify-between items-center p-3 bg-gray-50 rounded-md"
              {...(animated && {
                initial: { opacity: 0, x: -20 },
                animate: { opacity: 1, x: 0 },
                transition: { delay: index * 0.1 },
              })}
            >
              <div>
                <p className="font-medium text-gray-800">{schedule.courseName}</p>
                <p className="text-sm text-gray-600">
                  {schedule.lecturerName} â€¢ {schedule.roomName}
                </p>
              </div>
              <div className="text-right">
                <p className="text-sm font-medium text-gray-800">{schedule.dayName}</p>
                <p className="text-sm text-gray-600">
                  {schedule.startTime} - {schedule.endTime}
                </p>
              </div>
            </Wrapper>
          ))
        ) : (
          <p className="text-gray-500">No schedules available for today.</p>
        )}
      </div>
    </div>
  );
};

export default Schedules;